import React, { Component } from 'react';
import EpicMenu from './EpicMenu';
import './contact.scss';
import axios from 'axios';
import logo from './signature.svg';
import './contact_page.php'

const API_PATH = require('./contact_page.php');

class Contact extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fname: '',
      lname: '',
      email: '',
      message: '',
      mailSent: false,
      error: null
    }
  }

  handleFormSubmit( event ) {
    event.preventDefault();
    console.log(this.state);
  }

  handleFormSubmit = e => {
    e.preventDefault();
    axios({
      method: 'post',
      url: `${API_PATH}`,
      headers: { 'content-type': 'application/json' },
      data: this.state
    })
      .then(result => {
        this.setState({
          mailSent: result.data.sent
        })
      })
      .catch(error => this.setState({ error: error.message }));
  };
  
  
  render() {
    let links = [
      { label: 'Home', link: 'Home' },
      { label: 'Portfolio', link: '#Portfolio' },
      { label: 'Products', link: 'Products' },
      { label: 'Contact', link: 'Contact', active: true },
    ];

    return ([
      <div className="container center">
        <EpicMenu links={links} logo={logo}/>
      </div>,
      <div className="container center">
        <div className="wrapper">
          <div className="columncontact">
                <div>
                  <form action="/contact_page.php">
                    <label>First Name</label>
                    <input type="text" id="fname" name="firstname" placeholder="Your name.." 
                    value={this.state.fname}
                    onChange={e => this.setState({ fname: e.target.value })} 
                    />
                  </form>
                </div>
              
              <div>
                <form action="/action_page.php">
                    <label>Last Name</label>
                    <input type="text" id="lname" name="lastname" placeholder="Your last name.." 
                    value={this.state.lname}
                    onChange={e => this.setState({ lname: e.target.value })}
                    />
                  </form>
                  </div>
              
              <div>
              <form action="/action_page.php">
                    <label>Email</label>
                    <input type="email" id="email" name="email" placeholder="Your email"
                    value={this.state.email}
                    onChange={e => this.setState({ email: e.target.value })} 
                    />
                  </form>
              </div>
              
            </div>
            <div className="columncontact">
                <div>
                <form action="/action_page.php">
                    <label>Message</label>
                    <textarea id="subject" name="Message" placeholder="Write something.."
                    onChange={e => this.setState({ message: e.target.value })}
                    value={this.state.message}
                    ></textarea>
                    <input type="submit" onClick={e => this.handleFormSubmit(e)} value="Submit" />
                      <div>
                        {this.state.mailSent &&
                          <div>Thank you for contcting us.</div>
                        }
                      </div>
                  </form>
                </div>
            </div>
        </div>
      </div>]
    );
  }
}


export default Contact;
